import {
  LOG_IN,
  IS_AUTHED,
  CREATE_NEW_CLIENT
} from './actions'

const INITIAL_STATE = { clients: [], cred: {}, token: '', error: {} };

export default function(state = INITIAL_STATE, action) {
  switch(action.type){
    case LOG_IN:
      return { ...state,
        clients: action.payload.data.clients,
        cred: action.payload.data.admin,
        token: action.payload.data.token
      };
    case IS_AUTHED:
      return { ...state,
        ...state.token,
        clients: action.payload.data.clients,
        cred: action.payload.data.admin,
        error: action.payload.data.message
      };
    case CREATE_NEW_CLIENT:
      return { ...state,
        ...state.token,
        clients: action.payload.data.clients,
        error: action.payload.data.message
      };
    default:
      return state
  }
}


